p {
    margin: .3em 0;
  }

  h1 {
    font-size: 25px;
    margin-top: 0;
  }
h3{
    padding: 0;
}
  h4,
  h5,
  h6 {
    font-weight: normal;
  }

  ol,
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }

.bio{
position:relative;
}
.bio>h1{
    font-size:28px;
}
.bio>img{
    position:absolute;
    top:0;
    right:0;
}
#skills {
    width: 500px;
    height: 270px;
  }
  @media print {
    #skills{display:none;}
  }


  .skills>.wrapper {
    display: flex;
    justify-content: space-between;
  }

  .skills>.wrapper>:first-child {
    margin-right: 24px;
  }

  .skills>.wrapper>#skills {}

  .skills>.wrapper>ul {
    color: #666;
  }

  .skills>.wrapper strong {
    color: #f60;
  }

  .skills>.wrapper>ul>li {
    line-height: 1.2;
    margin-bottom: 8px;
  }
.projects p {
    color: #666;
  }
  .projects strong {
    color: #E62E2D;
  }
.projrcts>ol{
    margin-top:0;
    list-style-position: inside;
    padding:0;
}
.projects>ol>li {
    margin-bottom: 24px;
  }

.projects>ol>li>header{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.projects>ol>li>header>h3{
    display:inline-flex;
}
.projects>ol>li>header>span{
    display: inline-flex;
   
}
.projects>ol>li>header>span>a{
    margin: 0 10px;
   
}
.openSource>ul>li {
  
    margin-bottom: 24px;
  }
  .openSource>ul>li>p {
    color: #666;
   
  }
  .openSource header {
    position: relative;
    display: flex;
    justify-content: space-between;
  }
  .others>ul{
    color: #666;
    }
    @media (max-width: 500px) {
      body {
        margin: 0;
      }

      article {
        width: auto;
        height: auto;
      }

      .bio img {
        display: none;
      }

      .skills .wrapper {
        flex-direction: column;
      }

      #skills {
        width: auto;
      }
    }